<template>
    <div class="jifen">
      <div class="state">
		<div class="integral-wrap">
			<div class="integral">
				<div class="texts">
					<div class="active">100</div>
					<span>我的积分</span>
				</div>
			</div>
		</div>
		<div class="sign-bg"><img src="../../../static/imgs/sign-bg.png"/></div>
	  </div>
	  <div style="padding: .2rem;">
	  <ul class="imgs">
	  	<li @click="duihuan"><img src="../../assets/shops/imgs/img-duihuan.png"/></li>
	  	<li @click="lottery"><img src="../../assets/shops/imgs/img-lottery1.png"/></li>
	  	<div class="clear"></div>
	  </ul>
	  </div>
	  <div class="jifen-detail">
	  	<div class="text">积分明细</div>
	  	<ul>
	  		<li  v-for="item in items">
	  			<div class="top">
	  				<div v-if="item.type == 11">积分兑换商品</div>
	  				<div v-if="item.type == 12">积分参与大转盘</div>
	  				<div v-if="item.type == 13">购买商品获得积分</div>
	  				<div>{{item.number}}</div>
	  			</div>
	  			<p>{{$Num.Totime(item.create_time)}}</p>
	  		</li>
	  		
	  	</ul>
	  </div>
    </div>
</template>
<script>
  import { Toast,MessageBox,Indicator} from 'mint-ui';
  export default {
    data (){
      return {
          items:[
//			{name:'消费',sum:'+5',time:'2018-04-03 12:12:12'},
//			{name:'消费',sum:'+5',time:'2018-04-03 11:12:12'},
			]
      }
    },
    methods: {
     getJifenLog(page){
     	this.post('home/User/getJifenLog',{page:page,number:20}).then(function(res){  	 		
			console.log(res)
			if(res.data.code===10000){
    			this.items=res.data.info.data;    		
//  			if(type==11){
//  				this.items.type='积分兑换商品'
//  			}
//  			this.items.forEach(function(item){
//  				item.pic_url=this.imageURL(item.pic_url)
//  				
//  			}.bind(this))
    		}
			
    	}.bind(this))
     },
    
      duihuan(){
      	this.$router.push('/duihuan')
      },
      lottery(){
      	this.$router.push('/lottery')
      }
    },
    mounted(){
      this.getJifenLog(1)
    }
  }
</script>
<style scoped>
@import "../../../static/header.css";
/**/
.jifen-detail{background: #fff;}
.jifen-detail .text{padding: .2rem;border-bottom:1px #E6E6E6 solid;}
.jifen-detail ul{padding: .2rem;padding-bottom: 0;}
.jifen-detail li{padding: .2rem 0;border-bottom: 1px #e6e6e6 solid;}
.jifen-detail li:last-child{border-bottom: 0;}
.jifen-detail p{color: #999;}
.jifen-detail .top{display: flex;flex-direction: row;justify-content: space-between;align-items: center;margin-bottom: .1rem;}
.header img:last-child {
  width: 0.36rem;
  margin-right: 0.4rem;
}
.header span {
  margin: 0;
}
  .wallet {
    height: 100vh;
    background-color: #F2F2F2;
  }
  .main {
    padding: 0.2rem;
  }
  .wallet_card {
    padding: 0.6rem 0.4rem;
    background-color: #ff6f00;
    color: #FFFFFF;
    border-radius: 0.1rem;
  }
  .wallet_title {
    display: flex;
    justify-content: space-between;
    margin-bottom: 0.27rem;
  }
  .wallet_title>p {
    font-size: 0.24rem;
  }
  .wallet_card {
    position: relative;
    overflow: hidden;
  }
  .wallet_card h2 {
    font-size: 0.48rem;
    margin-bottom: 0.4rem;
  }
  .wallet_card img {
    width: 2.44rem;
    position: absolute;
    bottom: -0.2rem;
    right: 0;
  }
  .reflect {
    display: inline-block;
    height: 0.48rem;
    text-align: center;
    line-height: 0.48rem;
    width: 1.2rem;
    font-size: 0.3rem;
    background-color: #ffa800;
    border-radius: 0.2rem;
    color: #FFFFFF;
    margin-right: 0.1rem;
  }
  .way {
    height: 1.36rem;
    background-color: #FFFFFF;
    margin: 0.2rem 0;
    padding: 0 0.2rem;
    display: flex;
    justify-content: space-between;
    align-items: center;
  }
   .way_l p {
     font-size: 0.36rem;
     color: #111111;
     margin-bottom: 0.1rem;
  }
  .way_l span {
    color: #666666;
    font-size: 0.24rem;
  }
  /**/
 .jifen .state{position: relative;}
 .state{font-size: .25rem;background: linear-gradient(to right, #FF6984 , #FF4660);padding: 10px 15px;}
.state{font-size: .26rem;padding: 0;padding: .5rem 0;}
.sign-bg{position: absolute;bottom: 0;}
.sign-bg img{width: 100%;display: block;}
.integral-wrap{width: 1.8rem;height: 1.8rem;border-radius: 50%;background:rgba(255,255,255,.6);margin: auto;position: relative;}
.integral{width: 1.4rem;height: 1.4rem;border-radius: 50%;background: #fff;margin: auto;position: absolute;top: .2rem;left: .2rem;text-align: center;}
.integral .texts{height: 0.8rem;margin-top: .3rem;}
.integral .texts span{color: #666;font-size: .2rem;}

.imgs{padding-right: 10px;}
.imgs li{float: left;width: 50%;text-align: center;margin-right: 10px;text-align: left;}
.imgs li:nth-of-type(2n){margin-right: -10px;}
.imgs li img{display: block;width: 100%;}
.clear{clear: both;}

</style>



